.todo-page {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.todos {
  margin-top: 20px;
}

.todos p {
  color: var(--dark-alt);
  font-size: 16px;
  text-align: center;
  margin-top: 40px;
}

.todo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.filter-section {
  display: flex;
  align-items: center;
  gap: 10px;
}

.filter-icon {
  width: 20px;
  height: 20px;
  color: var(--light-alt);
}

.filter-select {
  background-color: var(--dark);
  color: var(--light);
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
}

.filter-select:focus {
  outline: none;
  box-shadow: 0 0 0 2px var(--primary);
} 